.SelectSet {
  width: 100vw;
  height: 100vh;
  background: var(--wet-asphalt);
  overflow: hidden;
  text-align: center;
  padding: 10px 10px;

  h3 {
    color: white;
    margin-bottom: 30px;
  }

  ul {
    font-size: 20px;
    font-weight: bold;
    padding-left: 0;
    max-width: 500px;
    width: 100%;
    margin: auto;

    li {
      position: relative;
      border: 1px solid white;
      margin-bottom: 5px;
      border-radius: 4px;
      cursor: pointer;
      &:hover {
        background: white;
      }

      a {
        width: 100%;
        height: 100%;
        display: inline-block;
        padding: 10px 10px;

      }
    }
  }
}
